/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

/* surrounding tag container */
.tag-editor {
  list-style-type: none;
  padding: 0 5px 0 0;
  margin: 0;
  overflow: hidden;
  border: 1px solid #eee;
  cursor: text;
  font: normal 14px sans-serif;
  color: #555;
  background: #fff;
  line-height: 20px;
}

/* core styles usually need no change */
.tag-editor li {
  display: block;
  float: left;
  overflow: hidden;
  margin: 3px 0;
}

.tag-editor div {
  float: left;
  padding: 0 4px;
}

.tag-editor .placeholder {
  padding: 0 8px;
  color: #bbb;
}

.tag-editor .tag-editor-spacer {
  padding: 0;
  width: 8px;
  overflow: hidden;
  color: transparent;
  background: none;
}

.tag-editor input {
  vertical-align: inherit;
  border: 0;
  outline: none;
  padding: 0;
  margin: 0;
  cursor: text;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  font-style: inherit;
  box-shadow: none;
  background: none;
  color: #444;
}

/* hide original input field or textarea visually to allow tab navigation */
.tag-editor-hidden-src {
  position: absolute !important;
  left: -99999px;
}

/* hide IE10 "clear field" X */
.tag-editor ::-ms-clear {
  display: none;
}

/* tag style */
.tag-editor .tag-editor-tag {
  padding-left: 5px;
  color: #46799b;
  background: #e0eaf1;
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
  border-radius: 2px 0 0 2px;
}

/* delete icon */
.tag-editor .tag-editor-delete {
  background: #e0eaf1;
  cursor: pointer;
  border-radius: 0 2px 2px 0;
  padding-left: 3px;
  padding-right: 4px;
}

.tag-editor .tag-editor-delete i {
  line-height: 18px;
  display: inline-block;
}

.tag-editor .tag-editor-delete i:before {
  font-size: 16px;
  color: #8ba7ba;
  content: "×";
  font-style: normal;
}

.tag-editor .tag-editor-delete:hover i:before {
  color: #d65454;
}

.tag-editor .tag-editor-tag.active + .tag-editor-delete, .tag-editor .tag-editor-tag.active + .tag-editor-delete i {
  visibility: hidden;
  cursor: text;
}

.tag-editor .tag-editor-tag.active {
  background: none !important;
}

