body {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  min-width: 320px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: -0.007em;
  color: #555;
  background: #fff;
}

body.is-gray {
  background-color: #f8f8f8;
}

.color-dark {
  color: #282828;
}

.color-blue {
  color: #3F36FF;
}

.md-show {
  display: none !important;
}

@media only screen and (max-width: 1023px) {
  .md-show {
    display: block !important;
  }
}

.md-hide {
  display: block !important;
}

@media only screen and (max-width: 1023px) {
  .md-hide {
    display: none !important;
