}

.out {
  position: relative;
  overflow: hidden;
}

.inner {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1320px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1023px) {
  .inner {
    padding: 0 15px;
  }
}

.inner_sm {
  max-width: 760px;
}

.inner_md {
  max-width: 1000px;
}

.wrap {
  padding: 45px 0 141px;
}

@media only screen and (max-width: 1023px) {
  .wrap {
    padding: 30px 0 70px;
  }
}

@media only screen and (max-width: 767px) {
  .wrap {
    padding: 30px 0;
  }
}

.block {
  display: flex;
}

.logo {
  position: relative;
  display: flex;
  align-items: center;
}

.logo img {
  display: block;
  max-width: 100%;
}

.logo__icons {
  width: 140px;
  transition: opacity .3s;
}

@media only screen and (max-width: 1023px) {
  .logo__icons {
    width: 126px;
  }
}

@media only screen and (max-width: 1023px) {
  .is-open-menu .logo__icons {
    opacity: 0;
  }
}

.logo__text {
  margin-left: 16px;
  width: 102px;
  transition: opacity .3s;
}

@media only screen and (max-width: 1259px) {
  .logo__text {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    opacity: 0;
  }
}

@media only screen and (max-width: 1023px) {
  .is-open-menu .logo__text {
    opacity: 1;
  }
}

.login {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

@media only screen and (max-width: 1023px) {
  .login {
    border-top: 1px solid #ECECEC;
    text-align: center;
  }
}

@media only screen and (max-width: 1023px) {
  .login__item {
    width: 50%;
  }
}

.login__item:not(:first-child) {
  margin-left: 25px;
}

@media only screen and (max-width: 1023px) {
  .login__item:not(:first-child) {
    margin-left: 0;
    border-left: 1px solid #ECECEC;
  }
}

.login__link {
  color: inherit;
  transition: color .3s;
}

.no-touch .login__link:hover {
  color: #3F36FF;
}

@media only screen and (max-width: 1023px) {
  .login__link {
    padding: 20px 19px;
    display: block;
  }
}

.login__item.is-active .login__link {
  color: #3F36FF;
}

@media only screen and (max-width: 1023px) {
  .nav {
    padding: 23px 15px 0;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  }
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

@media only screen and (max-width: 1023px) {
  .nav__list {
    padding: 30px 0;
    text-align: center;
    display: block;
  }
}

.nav__item {
  padding: 0 12px;
}

@media only screen and (max-width: 1023px) {
  .nav__item:not(:first-child) {
    margin-top: 30px;
  }
}

@media only screen and (max-width: 1023px) {
  .nav__item {
    display: block;
  }
}

.nav__link {
  color: inherit;
  transition: color .3s;
}

.no-touch .nav__link:hover {
  color: #3F36FF;
}

.nav__item.is-active .nav__link {
  color: #3F36FF;
}

.nav__login {
  display: none;
}

@media only screen and (max-width: 1023px) {
  .nav__login {
    margin: 0 -15px;
    display: flex;
  }
}

.search__button {
  position: relative;
  width: 60px;
  height: 60px;
  font-size: 0;
  line-height: 1;
  background-color: #fff;
  display: block;
}

.search__button .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -14px 0 0 -14px;
  font-size: 28px;
  color: #3F36FF;
}

.nav-search {
  font-size: 0;
  display: none;
}

@media only screen and (max-width: 1023px) {
  .nav-search {
    display: block;
  }
}

.nav-search__input {
  padding: 2px 8px;
  width: 100%;
  font-family: "gt-walsheim", Helvetica, Arial, sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.25;
  letter-spacing: -0.013em;
  color: #555;
  height: 38px;
  border-bottom: 1px solid #ECECEC;
  display: inline-block;
}

.nav-search__input::placeholder {
  color: rgba(85, 85, 85, 0.3);
}

.header {
  position: absolute;
  top: 30px;
  left: 0;
  z-index: 10;
  font-size: 15px;
  width: 100%;
}

@media only screen and (max-width: 1023px) {
  .header {
    top: 0;
    font-size: 20px;
  }
}

.header_static {
  position: relative;
  top: 0;
  padding: 30px 0;
}

@media only screen and (max-width: 1023px) {
  .header_static {
    padding-top: 0;
  }
}

.header__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

@media only screen and (max-width: 1023px) {
  .header__nav {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
    transition: opacity .3s, visibility .3s;
    background-color: #fff;
  }
  .header_static .header__nav {
    margin-top: -30px;
  }
  .is-open-menu .header__nav {
    opacity: 1;
    visibility: visible;
  }
}

.header__login:not(:last-child) {
  margin-right: 27px;
}

@media only screen and (max-width: 1023px) {
  .header__login:not(:last-child) {
    margin-left: 0;
  }
}

@media only screen and (max-width: 1023px) {
  .header__login {
    display: none;
  }
}

@media only screen and (max-width: 1023px) {
  .header__inner {
    padding: 0;
  }
}

.header__right {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header__search {
  border-left: 1px solid #E5E5E5;
}

@media only screen and (max-width: 1023px) {
  .header__search {
    display: none;
  }
}

.header__instruction {
  margin-right: 14px;
}

@media only screen and (max-width: 1023px) {
  .header__instruction {
    display: none;
  }
}

.cabin {
  position: relative;
  font-size: 0;
  vertical-align: top;
  display: inline-block;
}

.cabin:not(:last-child) {
  margin-right: 14px;
}

.cabin__logo {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  vertical-align: top;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  cursor: pointer;
  border: 1px solid #EFEFEF;
  display: inline-block;
}

.cabin__has-info {
  position: absolute;
  top: 2px;
  right: 0;
  width: 10px;
  height: 10px;
  border: 2px solid #fff;
  background-color: #3F36FF;
  border-radius: 50%;
}

.cabin__drop {
  position: absolute;
  top: 100%;
  left: 50%;
  margin: 25px 0 0 -94px;
  padding: 2px 14px;
  width: 188px;
  opacity: 0;
  visibility: hidden;
  user-select: none;
  transition: opacity .3s, visibility .3s;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  background-color: #fff;
}

@media only screen and (max-width: 1023px) {
  .cabin__drop {
    margin-left: -110px;
  }
}

.cabin.is-open .cabin__drop {
  opacity: 1;
  visibility: visible;
}

.cabin__drop .icon-login-arrow {
  position: absolute;
  top: -6px;
  left: 50%;
  font-size: 7px;
  margin-left: -14px;
  color: #fff;
}

@media only screen and (max-width: 1023px) {
  .cabin__drop .icon-login-arrow {
    margin-left: 2px;
  }
}

.cabin__item {
  display: block;
}

.cabin__item:not(:first-child) {
  border-top: 1px solid #E5E5E5;
}

.cabin__link {
  padding: 13px 0;
  font-family: "gt-walsheim", Helvetica, Arial, sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.15;
  color: #555;
  font-size: 15px;
  display: flex;
  transition: color .3s;
  justify-content: space-between;
  flex-wrap: wrap;
}

.cabin__item.is-active .cabin__link {
  color: #282828;
  font-weight: 500;
}

.cabin__item.is-logout .cabin__link {
  color: rgba(40, 40, 40, 0.5);
}

.no-touch .cabin__link:hover {
  color: #3F36FF;
}

.cabin__rating, .cabin__mess {
  margin-left: 10px;
}

.cabin__rating {
  font-weight: 400;
  color: rgba(85, 85, 85, 0.5);
}

.cabin__rating .icon {
  margin-top: 1px;
  font-size: 14px;
  vertical-align: top;
  display: inline-block;
}

.cabin__mess {
  color: #3F36FF;
}

