.logo {
	position: relative;
	display: flex;
	align-items: center;
	img {
		display: block;
		max-width: 100%; }
	&__icons {
		width: 140px;
		transition: opacity .3s;
		@include md {
			width: 126px; }
		.is-open-menu & {
			@include md {
				opacity: 0; } } }
	&__text {
		margin-left: 16px;
		width: 102px;
		transition: opacity .3s;
		@include lg {
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			opacity: 0; }
		.is-open-menu & {
			@include md {
				opacity: 1; } } } }

.login {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	@include md {
		border-top: 1px solid $gallery;
		text-align: center; }
	&__item {
		@include md {
			width: 50%; }
		&:not(:first-child) {
			margin-left: 25px;
			@include md {
				margin-left: 0;
				border-left: 1px solid $gallery; } } }
	&__link {
		color: inherit;
		transition: color .3s;
		@include hover {
			color: $blue; }
		@include md {
			padding: 20px 19px;
			display: block; }
		.login__item.is-active & {
			color: $blue; } } }
.nav {
	@include md {
		padding: 23px 15px 0;
		box-shadow: 0 4px 16px rgba($black, 0.1); }
	&__list {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		@include md {
			padding: 30px 0;
			text-align: center;
			display: block; } }
	&__item {
		padding: 0 12px;
		&:not(:first-child) {
			@include md {
				margin-top: 30px; } }
		@include md {
			display: block; } }
	&__link {
		color: inherit;
		transition: color .3s;
		@include hover {
			color: $blue; }
		.nav__item.is-active & {
			color: $blue; } }
	&__login {
		display: none;
		@include md {
			margin: 0 -15px;
			display: flex; } } }

.search {
	&__button {
		position: relative;
		width: 60px;
		height: 60px;
		font-size: 0;
		line-height: 1;
		background-color: $white;
		display: block;
		.icon {
			position: absolute;
			top: 50%;
			left: 50%;
			margin: -14px 0 0 -14px;
			font-size: 28px;
			color: $blue; } } }

.nav-search {
	font-size: 0;
	display: none;
	@include md {
		display: block; }
	&__input {
		padding: 2px 8px;
		width: 100%;
		font-family: $walsheim;
		@include f(20px,400,1.25);
		letter-spacing: -0.013em;
		color: $emperor;
		height: 38px;
		border-bottom: 1px solid $gallery;
		display: inline-block;
		&::placeholder {
			color: rgba($emperor, .3); } } }

.header {
	position: absolute;
	top: 30px;
	left: 0;
	z-index: 10;
	font-size: 15px;
	width: 100%;
	@include md {
		top: 0;
		font-size: 20px; }
	&_static {
		position: relative;
		top: 0;
		padding: 30px 0;
		@include md {
			padding-top: 0; } }
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		box-shadow: 0 4px 16px rgba($black, 0.1);
		background-color: $white; }
	&__nav {
		@include md {
			position: absolute;
			top: 100%;
			left: 0;
			width: 100%;
			opacity: 0;
			z-index: -1;
			visibility: hidden;
			transition: opacity .3s, visibility .3s;
			background-color: $white;
			.header_static & {
				margin-top: -30px; }
			.is-open-menu & {
				opacity: 1;
				visibility: visible; } } }
	&__login {
		&:not(:last-child) {
			margin-right: 27px;
			@include md {
				margin-left: 0; } }
		@include md {
			display: none; } }
	&__inner {
		@include md {
			padding: 0; } }
	&__right {
		display: flex;
		flex-wrap: wrap;
		align-items: center; }
	&__search {
		border-left: 1px solid $mercury;
		@include md {
			display: none; } }
	&__instruction {
		margin-right: 14px;
		@include md {
			display: none; } } }

.cabin {
	position: relative;
	font-size: 0;
	vertical-align: top;
	display: inline-block;
	&:not(:last-child) {
		margin-right: 14px; }
	&__logo {
		width: 40px;
		height: 40px;
		border-radius: 50%;
		vertical-align: top;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		cursor: pointer;
		border: 1px solid #EFEFEF;
		display: inline-block; }
	&__has-info {
		position: absolute;
		top: 2px;
		right: 0;
		width: 10px;
		height: 10px;
		border: 2px solid $white;
		background-color: $blue;
		border-radius: 50%; }
	&__drop {
		position: absolute;
		top: 100%;
		left: 50%;
		margin: 25px 0 0 -94px;
		padding: 2px 14px;
		width: 188px;
		opacity: 0;
		visibility: hidden;
		user-select: none;
		transition: opacity .3s, visibility .3s;
		box-shadow: 0 4px 20px rgba(0,0,0,.12);
		border-radius: 8px;
		background-color: $white;
		@include md {
			margin-left: -110px; }
		.cabin.is-open & {
			opacity: 1;
			visibility: visible; }
		.icon-login-arrow {
			position: absolute;
			top: -6px;
			left: 50%;
			font-size: 7px;
			margin-left: -14px;
			color: $white;
			@include md {
				margin-left: 2px; } } }
	&__item {
		display: block;
		&:not(:first-child) {
			border-top: 1px solid $mercury; } }
	&__link {
		padding: 13px 0;
		font-family: $walsheim;
		@include f(15px,400,1.15);
		color: $emperor;
		font-size: 15px;
		display: flex;
		transition: color .3s;
		justify-content: space-between;
		flex-wrap: wrap;
		.cabin__item.is-active & {
			color: $dark;
			font-weight: 500; }
		.cabin__item.is-logout & {
			color: rgba($dark, 0.5); }
		@include hover {
			color: $blue; } }
	&__rating,
	&__mess {
		margin-left: 10px; }
	&__rating {
		font-weight: 400;
		color: rgba($emperor, 0.5);
		.icon {
			margin-top: 1px;
			font-size: 14px;
			vertical-align: top;
			display: inline-block; } }
	&__mess {
		color: $blue; } }
