//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

// WALSHEIM
$walsheim: "gt-walsheim";
@font-face {
	@include fontdefault(normal, 900, $walsheim);
	@include fontpath('gt-walsheim-ultra-bold', 'gt-walsheim-ultra-bold', gt-walsheim-ultra-bold); }
@font-face {
	@include fontdefault(italic, 900, $walsheim);
	@include fontpath('gt-walsheim-ultra-bold-oblique', 'gt-walsheim-ultra-bold-oblique', gt-walsheim-ultra-bold-oblique); }

