.check {
	&__row {
		margin: 20px -10px 0; }
	&__col {
		padding: 0 10px; } }


.sidebar {
	background-color: $white;
	border: 1px solid $gallery;
	@include md {
		margin-bottom: 30px; }
	&__top {
		padding: 20px;
		display: flex;
		@include lg {
			justify-content: space-between; }
		@include md {
			justify-content: flex-start;
			padding: 20px 30px; }
		@include sm {
			padding: 20px 15px; }
		@include xs {
			justify-content: space-between; } }
	&__inner {
		@include md {
			padding: 0 30px; }
		@include sm {
			padding: 0 15px; } }
	&__edit {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 10px;
		width: 22px;
		height: 22px;
		// background-color: #555
		opacity: 0;
		visibility: hidden;
		transition: opacity .3s, visibility .3s;
		color: rgba($emperor,.5); }
	&__block {
		position: relative;
		padding: 16px 0 20px;
		border-top: 1px solid $wildSand;
		z-index: 1;
		@include hover {
			.edit-block {
				opacity: 1;
				visibility: visible; } } }
	&__tags {
		&:not(:first-child) {
			margin-top: 5px; } } }
