.hero {
	position: relative;
	margin-bottom: 65px;
	padding-top: 155px;
	overflow: hidden;
	background-color: $dodgerBlue;
	@include md {
		padding: 313px 0 15px; }
	@include sm {
		margin-bottom: 25px;
		padding-bottom: 8px; }
	&__inner {
		display: flex;
		@include md {
			justify-content: center; }
		// +sm
 }		// 	display: block
	&__wrap {
		margin-bottom: 65px;
		padding: 35px 40px 40px;
		max-width: 650px;
		min-width: 597px;
		align-self: flex-start;
		background-color: $white;
		@include md {
			position: relative;
			margin-bottom: 0;
			padding-bottom: 0;
			z-index: 1;
			max-width: 590px;
			min-width: auto; }
		@include sm {
			margin: 0 -10px;
			width: 100%;
			padding: 8px 10px 0; } }
	&__separator {
		margin: 0 17px;
		color: $boulder;
		text-transform: uppercase;
		@include md {
			display: none; } }
	&__footer {
		margin-top: 180px;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		@include md {
			margin: 90px -40px 0; }
		@include sm {
			margin: 45px -10px 0; } }
	&__btn.btn {
		@include md {
			width: 50%;
			padding: 17px 20px;
			border: 2px solid $blue;
			text-align: center;
			@include hover {
				color: $white;
				background-color: $blue; } }
		@include sm {
			width: 50%;
			padding: 13px 30px; }
		.btn__text {
			border-bottom: none; }
		&:not(:first-child) {
			@include md {
				margin-left: -2px;
				width: calc(50% + 2px); } } }
	&__img {
		position: relative;
		z-index: 1;
		pointer-events: none;
		margin-top: -78px;
		margin-left: -43px;
		max-width: 537px;
		max-height: 563px;
		overflow: hidden;
		align-self: flex-end;
		@include md {
			position: absolute;
			top: 55px;
			z-index: 0;
			margin-top: 0; }
		@include sm {
			left: 50%;
			margin-left: -108px;
			width: 275px; }
		img {
			max-width: 100%;
			display: block; } } }

.hero-slider {
	margin-bottom: 65px;
	color: $white;
	overflow: hidden;
	white-space: nowrap;
	opacity: 0;
	visibility: hidden;
	transition: opacity .5s, visibility .5s;
	&.slick-initialized {
		opacity: 1;
		visibility: visible; }
	@include sm {
		margin-bottom: 25px; }
	.slick-dots {
		position: absolute;
		left: 50%;
		bottom: 23px;
		transform: translateX(-50%);
		padding: 0 20px;
		width: 100%;
		max-width: 1320px;
		@include md {
			padding: 0 15px; }
		li {
			display: inline-block;
			&:not(:first-child) {
				margin-left: 8px; }
			&.slick-active {
				button:before {
					animation: lineOn 4s linear alternate forwards; } } }
		button {
			position: relative;
			width: 72px;
			height: 2px;
			font-size: 0;
			background-color: rgba($white, .4);
			overflow: hidden;
			display: block;
			&:before {
				position: absolute;
				content: "";
				top: 0;
				left: 0;
				transform: translateX(-100%);
				width: 100%;
				height: 2px;
				animation: lineOut 0.2s linear alternate forwards;
				background-color: $white; } } }

	&__slide {
		display: inline-block; }
	&__wrap {
		position: relative;
		padding: 318px 0 59px;
		border: 1px solid $gallery;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		&:before {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: rgba(0,0,0,0.25); } }
	&__label {
		font-size: 14px;
		margin-bottom: 10px; }
	&__inner {
		position: relative; } }

@keyframes lineOn {
	0% {
		transform: translateX(-100%); }
	100% {
		transform: translateX(0); } }
@keyframes lineOut {
	0% {
		transform: translateX(0); }
	100% {
		transform: translateX(100%); } }
